import React, { createElement, forwardRef, useRef } from 'react'
import Box from '@mui/material/Box'
import { useState, useEffect } from 'react'

import { isMobile } from 'react-device-detect'
import { ThemeProvider, StyledEngineProvider } from '@mui/material/styles';

import { grey } from '@mui/material/colors'

import Typography from '@mui/material/Typography'
import Avatar from '@mui/material/Avatar'
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight'


import useStyles, {sxStyle} from './carouselStyle'
import FrontOfficeTheme from '../../style/globalTheme'
import Tippy from '@tippyjs/react';

import 'tippy.js/themes/light.css'; // optional 


import SwiperCore, { Navigation, Pagination, Scrollbar, A11y, Lazy } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';
// Import Swiper styles
import 'swiper/swiper.scss';
import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/scrollbar/scrollbar.scss';

const debounce = (fn, ms) => {
    let timer
    return _ => {
        clearTimeout(timer)
        timer = setTimeout(_ => {
            timer = null
            fn()
        }, ms)
    };
}




SwiperCore.use([Navigation, Pagination, Scrollbar, A11y])

const Carousel = (props) => {

    const { noAvatar, cellTemplate, cellsData, carouselConfig, isMounted: parentIsMounted, sideData, ssrTest, blurred } = props



    const innerGap = 10 // px

    const [swiper, updateSwiper] = useState(null)
    const position = swiper?.realIndex
    const [currentIndex, updateCurrentIndex] = useState(0);
    const [imageSet, updateImageSet] = useState(0)
    const [isBlurred, setIsBlurred] = useState(false)
    const [deviceState, setDeviceState] = useState(null)

    useEffect(() => {
        if (isBlurred != blurred) {


            setIsBlurred(blurred)

            const timer = setTimeout(_ => {
                setIsBlurred(false)

            }, 500)


            return () => {
                clearTimeout(timer)
            }
        }

    }, [blurred])


    useEffect(() => {


        const slideChange = () => {
            updateCurrentIndex(swiper.realIndex)
        }

        const slideChangeTransitionStart = () => {
            updateCurrentIndex(swiper.realIndex)

        }

        if (swiper !== null && !swiper.destroyed == true) {
            swiper.on("slideChange", slideChange);
            swiper.on("slideChangeTransitionStart", slideChangeTransitionStart)
        }
    }, [swiper])

    useEffect(() => {


        if (swiper !== null && !swiper.destroyed == true) {
            swiper.update();
            swiper.slideTo(0);

            const shouldPaginationHide = cellsData[imageSet].length < 2;
            const isPaginationHidden = swiper.pagination.$el.hasClass(
                swiper.params.pagination.hiddenClass
            );


            if (shouldPaginationHide && !isPaginationHidden) {
                swiper.emit("paginationHide", swiper);
            }

            if (!shouldPaginationHide && isPaginationHidden) {
                swiper.emit("paginationShow", swiper);
            }

            if (shouldPaginationHide !== isPaginationHidden) {
                swiper.pagination.$el.toggleClass(swiper.params.pagination.hiddenClass);
            }

            swiper.lazy.load();
        }
    }, [imageSet])

   
   



 






    const prevRef = useRef(null);
    const nextRef = useRef(null)


    const TippyContent = forwardRef((props, ref) => {
        return (<Box ref={ref} width="25px" height="25px" marginLeft="10px" position="relative" bottom="5px">
            {createElement(carouselConfig.icon)}
        </Box>)
    })

    

    let params = {
        onIndexChanged: index => console.log('CHANGED'),
        slidesPerView: 1.25,
        slidesPerGroup: 2,
        modules: [Pagination, Navigation, Lazy],
        preloadImages: false,
        lazy: true,
        pagination: {
            el: ".swiper-pagination",
            type: "bullets",
            clickable: true
        },
        navigation: {
            nextEl: nextRef.current,
            prevEl: prevRef.current
        },
        loop: false,
        spaceBetween: innerGap,
        onSwiper: updateSwiper,
        breakpoints: carouselConfig.breakpoints
    }

    return (
        <StyledEngineProvider injectFirst>
            <ThemeProvider theme={FrontOfficeTheme}>
            
                {(ssrTest === false && parentIsMounted === 2) && <Box  width={carouselConfig.titleBorderBottom} height={carouselConfig.carouselHeight + 'px'} >
                    <Box display="flex" flexDirection="row" alignContent="center" alignItems="center" borderBottom={carouselConfig.titleBorderBottom} marginBottom={carouselConfig.titleMarginBottom} >
                        <Box padding="20px 0px 20px 0px" flex={7}>
                            <Box display="flex" flexDirection="row">

                                <Typography variant="h1" sx={carouselConfig.titleStyle} >
                                    {carouselConfig.title} 

                                </Typography>

                                {carouselConfig.icon && <Box width="25px" height="25px" marginLeft="10px" position="relative" bottom="5px">
                                    {createElement(carouselConfig.icon)}
                                </Box>}
                            </Box>

                        </Box>

                    </Box>
                    <Box width="100%">
                        <Box display="flex" flexDirection="row" alignContent="center" alignItems="center" width="100%" height={`${carouselConfig.carouselHeight}px`}>
                            <Box flexWrap="nowrap" overflow="hidden" width="100%" display="flex" flexDirection="row" height={`${carouselConfig.carouselHeight}px`}>
                                {[1, 2, 3, 4, 5, 6, 7].map(index => <Box display="flex" flexDirection="row" width="300px" key={index} padding="20px" alignContent="center" alignItems="center">
                                    <Box width="280px" height={`${carouselConfig.carouselHeight}px`} bgcolor={grey[300]}>
                                        {!noAvatar && <Box padding="10px" display="flex" flexDirection="row" alignContent="center" alignItems="center">


                                            <Avatar style={{ backgroundColor: grey[400] }} >
                                                <Box width="20px" height="20px" bgcolor={grey[400]}>

                                                </Box>
                                            </Avatar>
                                            <Box width={80} height={20} color={grey[400]} bgcolor={grey[400]} marginLeft="10px">
                                                @
                                            </Box>

                                        </Box>}
                                        <Box marginTop="70px" display="flex" flexDirection="column" alignContent="center" alignItems="center">
                                            <Box margin="5px" width={60} height={20} color={grey[400]} bgcolor={grey[400]} marginLeft="10px">
                                                @
                                            </Box>
                                            <Box margin="5px" width={100} height={20} color={grey[400]} bgcolor={grey[400]} marginLeft="10px">
                                                @
                                            </Box>
                                        </Box>

                                    </Box>



                                </Box>)}

                            </Box>



                        </Box>

                    </Box>


                </Box>}


                {cellsData.length > 0 && <Box height={(cellsData.length === 0) ? '0px' : "100%"} width='100%' >

                    <Box display="flex" flexDirection="row" alignContent="center" alignItems="center" borderBottom={(cellsData.length === 0) ? "none" : carouselConfig.titleBorderBottom} marginBottom={(cellsData.length === 0) ? "none" : carouselConfig.titleMarginBottom}>
                        <Box visibility={((cellsData.length === 0)) ? "hidden" : "visible"} padding="20px 0px 20px 0px" flex={7}>
                            <Box display="flex" flexDirection="row" >


                                <Typography variant="h1" sx={carouselConfig.titleStyle} >
                                    {carouselConfig.title} 
                                </Typography>

                                {carouselConfig.icon && <Tippy delay={500} theme='light' content={carouselConfig.tippyContent}><TippyContent /></Tippy>}
                            </Box>

                        </Box>

                    </Box>
                    <Box width="100%" >

                        <Box display="flex" flexDirection="row" alignContent="center" alignItems="center" width="100%" padding="30px 0px 20px 0px">

                            {!isMobile && <Box padding="5px" position="relative" right="10px">
                                <button ref={prevRef} style={{ backgroundColor: grey[300], width: '20px', height: '20px', border: 'none', borderRadius: '10px' }}>
                                    <KeyboardArrowLeftIcon style={{ fontSize: '25px', color: "white", position: "relative", right: '9px', bottom: '7px' }} />
                                </button>
                            </Box>}

                            <Box width="calc(100% - 25px)" >

                                <Swiper
                                    {...params}

                                >
                                    {cellsData.map((data, index) => (<SwiperSlide key={index}>
                                        <Box width={(carouselConfig.carouselHeight / carouselConfig.cellRatio) + 'px'} height={carouselConfig.carouselHeight + 'px'} >
                                            {createElement(cellTemplate, {
                                                'parentIsMounted': parentIsMounted,
                                                'index': index,
                                                'position': position,
                                                'data': data,
                                                'type': carouselConfig.type,
                                                'carouselHeight': carouselConfig.carouselHeight,
                                                'key': index,
                                                'sideData': sideData,
                                                'blurred': isBlurred
                                            })}
                                        </Box></SwiperSlide>))}
                                </Swiper>
                            </Box>
                            {!isMobile && <Box padding="5px" position="relative" left="10px">
                                <button ref={nextRef} style={{ backgroundColor: grey[300], width: '20px', height: '20px', border: 'none', borderRadius: '10px' }}>
                                    <KeyboardArrowRightIcon style={{ fontSize: '25px', color: "white", position: "relative", right: '8px', bottom: '7px' }} />
                                </button>
                            </Box>}


                        </Box>

                    </Box>

                </Box>}
            </ThemeProvider >
        </StyledEngineProvider>
    );

}

export default Carousel






